me (gameId, gameConfig) {\r\n      console.log('User Game:', gameId)\r\n      api.nowUser.setGameId(gameId)\r\n      api.nowUser.setGameConfig(gameConfig)\r\n      router.push('/choose')\r\n    }\r\n  },\r\n  created: function () {\r\n    // load gameList and use promise to change the gameList\r\n    api.nowUser.resetState()\r\n    enterApi.getGameIdList()\r\n      .then((function (res) {\r\n        for (let game of res.data.gameList) {\r\n          this.gameList.unshift({\r\n            index: game.gameId,\r\n            text: game.gameConfig.title,\r\n            describe: game.gameConfig.describe,\r\n            gameConfig: game.gameConfig\r\n          })\r\n        }\r\n      }).bind(this))\r\n      .catch(function (err) {\r\n        console.error(err)\r\n      })\r\n  }\r\n}\r\n</script>\r\n\r\n<style>\r\n.home {\r\n  min-height: 100%;\r\n}\r\n\r\n.home .head {\r\n  padding-top: 60px;\r\n  padding-bottom: 60px;\r\n}\r\n\r\n.home .layout {\r\n  padding-left: 14px;\r\n  padding-right: 14px;\r\n}\r\n\r\n.home .card {\r\n  height: 100% !important;\r\n}\r\n\r\n.home .headline {\r\n  display: block;\r\n  width: 100%;\r\n}\r\n\r\n.home .card__text {\r\n  padding-right: 0;\r\n  padding-left: 0;\r\n}\r\n\r\n.home .list__tile__title {\r\n  color: rgba(0, 0, 0, 0.87);\r\n}\r\n\r\n.home .flex {\r\n  margin-bottom: 8px;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);

